.profile {
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		width: 50vw;
		margin: 0 auto;
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		&__item-data {
			color: #999;
		}
	}

	&__item {
		display: flex;
		justify-content: space-between;
		width: 100%;
		border-bottom: 1px solid #eaeaea;
		padding-bottom: 10px;
		margin-bottom: 10px;

		&:last-child {
			border: none;
		}
	}

	&__header {
		text-align: center;
		margin-bottom: 60px;
	}

	&__avatar {
		border-radius: 50%;
		margin-bottom: 21px;
	}

	&__content {
		width: 100%;
		margin-bottom: 60px;
	}

	&__footer {
		width: 100%;
		display: flex;
		flex-direction: column;

		&-item {
			width: 25%;
			border: none;
			background: none;
			text-align: left;
			font-size: 16px;
			color: #3369f3;
			cursor: pointer;
			margin-bottom: 20px;

			&--logout {
				color: #f00;
			}
		}
	}
}
