@import "../variables";

.chats-sidebar {
	height: 100%;
	width: 25vw;
	border-right: 1px solid #efefef;
	background: #fbfbfb;
	position: fixed;
	top: 0;
	left: 0;
}

.chats-header {
	display: flex;
	flex-wrap: wrap;
	background: #4e54c8; /* fallback for old browsers */
	background: linear-gradient(to right, #4e54c8, #8f94fb); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #4e54c8, #8f94fb);
	height: 100px;
	color: #4e54c8;

	&__search {
		width: 90%;
		height: 32px;
		margin: 0 auto;
		border-radius: 5px;
		background: #fff;
	}

	&__input {
		width: 100%;
		height: 100%;
		align-self: center;
		font-size: 14px;
		line-height: 12px;
		border: none;
		background: none;
		padding-left: 1vw;

		&::placeholder {
			color: #4e54c8;
		}

		&:focus {
			outline: none;
			color: #4e54c8;
		}
	}

	&__profile-link {
		color: #fff;
		font-size: 14px;
		line-height: 12px;
		text-decoration: none;
		margin: 23px 10px 0 auto;
	}
}

.chats-list {
	padding: 0;
	margin: 0;
}

.chat {
	border-bottom: 1px solid #efefef;
	padding-top: 12px;
	padding-bottom: 12px;
	width: 100%;
	max-height: 70px;
	display: flex;
	list-style: none;

	&__item {
		margin-top: 10px;

		.user-avatar {
			width: 60px;
			height: 60px;
			border-radius: 50%;
			margin-left: 10px;
		}

		.user-name {
			padding-left: 10px;
			padding-bottom: 4px;
		}

		.user-message {
			padding-left: 10px;
			color: #999;
		}

		&:last-child {
			margin-left: auto;
			margin-right: 10px;
		}
	}

	&__info {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		text-align: right;
	}

	&__messages-count {
		width: 21px;
		height: 21px;
		color: #fff;
		text-align: center;
		font-size: 14px;
		background: #4e54c8;
		border-radius: 50%;
		align-self: flex-end;

		span {
			vertical-align: middle;
		}
	}

	&__time {
		font-size: 11px;
		line-height: 13px;
		color: #999;
	}
}

.messages {
	display: flex;
	flex-direction: column;
	width: 75vw;
	margin-left: 25vw;

	&__main {
		display: flex;
		flex-direction: column;
		justify-content: end;
		width: 75vw;
		height: 100vh;
		position: fixed;
		bottom: 0;
		margin-bottom: 80px;
	}

	&__header {
		width: 75vw;
		height: 70px;
		display: flex;
		justify-content: space-between;
		position: fixed;
		z-index: 1000;
		top: 0;
		border-bottom: 2px solid #efefef;
		background: #fff;
	}

	.user {
		display: flex;
		align-items: center;
		margin: 10px 20px;

		&__avatar {
			width: 50px;
			height: 50px;
			border-radius: 50%;
			margin-right: 10px;
		}
	}

	&__settings {
		display: flex;
		align-items: center;
		margin: 10px 20px;

		&::after {
			content: "\22EE";
			font-size: 25px;
		}
	}

	&__friend-container {
		max-width: 25vw;
		background: #f8f8f8;
		border-radius: 10px;
		font-size: 14px;
		line-height: 15px;
		color: #1e1e1e;
		margin-left: 20px;
		margin-top: 30px;
		padding: 11px;
	}

	&__date {
		text-align: center;
		font-size: 14px;
		line-height: 12px;
		color: #999;
		margin-top: 48px;
	}

	&__mine-container {
		max-width: 25vw;
		font-size: 14px;
		line-height: 15px;
		color: #1e1e1e;
		margin-left: auto;
		margin-right: 20px;
		padding: 11px;
		background: #e4edfd;
		border-radius: 10px;
	}

	&__file {
		padding-left: 20px;

		img {
			cursor: pointer;
		}
	}

	&__footer {
		width: 75vw;
		height: 70px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: fixed;
		bottom: 0;
		z-index: 1000;
		margin: 20px 0 0;
	}

	#addFile {
		display: none;
	}

	&__text {
		display: flex;
		align-items: center;
		width: 63vw;
		height: 30px;
		line-height: 30px;
		outline: none;
		resize: none;
		border-radius: 30px;
		border: none;
		background-color: #efefef;
		padding-left: 20px;
	}

	&__send-btn {
		width: 35px;
		height: 35px;
		color: #fff;
		background-color: #4e54c8;
		border: none;
		border-radius: 50%;
		margin-right: 20px;
		cursor: pointer;
	}
}
