.error {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;

	&__number {
		font-weight: 500;
		font-size: 40px;
		line-height: 40px;
		color: #1e1e1e;
		margin-bottom: 20px;
	}

	&__comment {
		font-weight: 500;
		font-size: 20px;
		line-height: 20px;
		color: #1e1e1e;
		margin-bottom: 69px;
	}

	&__link {
		font-size: 14px;
		line-height: 11px;
		color: #3369f3;
	}
}
